/* ------------------------projects page----------------------- */
/* -------------------------------------------------------------*/

.title {
  font-size: 4em;
  /* padding-top: 8%; */
  display: block;
}

.project-container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.project-wrapper {
  position: relative;
  /* margin: 50px 0 45px; */
}

.project-items {
  margin-bottom: 24px;
}

.projects-item {
  display: flex;
  flex: 1 1;
  align-items: baseline;
  margin: 0 1rem;
  border-radius: 10px;
}

.project-link {
  display: flex;
  flex-flow: column;
  width: 500px;
  box-shadow: 0 6px 20px #f5dddd;
  -webkit-filter: drop-shadow(0 6px 20px#f5dddd);
  filter: drop-shadow(0 6px 20px #f5dddd);
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
  padding-bottom: 20px;
}

.fade-img {
  -webkit-animation-name: fade-img;
          animation-name: fade-img;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.project-tags {
  position: relative;
  width: 100%;
  padding-top: 50%;
  overflow: hidden;
}

.project-tags::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 15px;
  font-weight: 700;
  color: black;
  background-color: #f5dddd;
  box-sizing: border-box;
}

.project-img {
  position: absolute;
  border-radius: 40px;
  cursor: pointer;
  border: 2px solid black;
  top: 0;
  /* right: 0; */
  /* bottom: 0;
  left: 0; */
  display: block;
  width: 400px;
  max-width: 100%;
  height: 350px;
  max-height: 100%;
  /* object-fit: fill; */
  transition: all 0.2s linear;
  display: flex;
  align-items: baseline;
}

.project-img:hover {
  transform: scale(0.95);
  opacity: 0.5;
}

.project-info {
  padding-left: 30px;
}

.project-text {
  color: black;
  font-size: 25px;
  /* line-height: 20px; */
  width: 450px;
  align-items: center;
}

.instru {
  color: black;
  text-align: center;
  margin-top: 0px;
  margin-bottom: 0px;
}

